import fs from "fs";
import path from "path";
import Link from "next/link";

interface WeekPageProps {
  params: {
    division: string;
    week: string;
  };
}

export default function WeekPage({ params }: WeekPageProps) {
  const { division, week } = params;

  // Build folder path in public
  const dir = path.join(process.cwd(), `public/d${division}/w${week}`);
  let files: string[] = [];

  if (fs.existsSync(dir)) {
    files = fs.readdirSync(dir).filter(f => f.endsWith(".html"));
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-800 p-6">
      <div className="bg-gray-500 rounded-xl shadow-lg p-8 w-full max-w-xl text-center">
        <h1 className="text-3xl font-bold mb-6 text-gray-800">
          Division {division} - Week {week} Replays
        </h1>

        {files.length === 0 && <p className="text-white">No replays uploaded yet.</p>}

        <ul className="text-left">
          {files.map(file => (
            <li key={file} className="mb-2">
              <Link
                href={`/d${division}/w${week}/${file}`}
                className="text-blue-400 hover:text-blue-600 underline"
              >
                {file.replace(".html", "")}
              </Link>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}